* {
    padding: 0;
    margin: 0;
}

body {
    background-color: rgba(111, 166, 179, 0.585);
}

.navigation-container {
    width: 100%;
    background-color: rgb(60, 113, 219);
}

.navigation-items {
    display: flex;
}

.navigation-item {
    padding: 20px 40px;
    color: black;
    text-decoration: none;
    transition: background-color 0.3s ease-in-out;
}

.navigation-item:hover {
    background-color: rgba(75, 175, 198, 0.585);
}

.body-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.homepage-banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url(./images/banner.jpg) no-repeat center center fixed;
}

.homepage-title {
    color: white;
    font-size: 80px;
    margin: 30px 0px 0px;
}

.homepage-subtitle {
    color: white;
    font-size: 30px;
    margin: 0px 0px 30px;
}

.carousel-container {
    position: relative;
    width: 800px;
    height: 350px;
    overflow: hidden;
    margin: 40px 0px;
}

.carousel-container > button {
    position: absolute;
    bottom: 0;
    width: 100px;
    height: 350px;
    background: rgba(51, 51, 51, 0.5);
    box-shadow: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 30px;
    z-index: 10;
}

.carousel-container > button:nth-child(1) {
    left: 0;
}

.carousel-container > button:nth-child(2) {
    right: 0;
}

.carousel-slides {
    display: flex;
}

.carousel-img {
    width: 800px;
    height: auto;
    flex-shrink: 0;
    object-fit: cover;
    transition: transform 0.5s ease-in-out;
}

.about-us-title {
    margin-top: 50px;
}

.about-us-body {
    font-size: 20px;
    width: 1000px;
    margin-top: 50px;
    text-align: center;
    line-height: 1.5
}

.game-title {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

.game-slider {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 50px;
}

.game-full-container {
    display: flex;
    justify-content: center;
    gap: 50px;
    margin-top: 50px;
}

.game-original {
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-position: left top;
}

.game-container {
    display: grid;
    gap: 2px;
    position: relative;
    margin-bottom: 50px;
}

.game-piece {
    cursor: pointer;
    transition: opacity 0.3s ease;
}

.articles-title {
    margin: 30px 0px;
}

.article-card-area {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 1000px;
    gap: 50px;
}

.article-card-container {
    margin-top: 50px;
    width: 250px;
    height: 250px;
    border-radius: 30px;
    overflow: hidden;
}

.article-card {
    position: relative;
}

.article-card-text {
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
    background: rgba(75, 175, 198, 0.585);
    padding: 10px;
    box-sizing: border-box;
    width: 250px;
    height: 100px;
    z-index: 10;
}

.article-card-img {
    width: 250px;
    height: 250px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.article-card-img > img {
    width: auto;
    height: 250px;
}

.article-heading {
    position: relative;
    width: 100%;
    height: 200px;
    overflow: hidden;
    transition: all 0.3s ease;
}

.article-heading.small {
    height: 100px;
    position: fixed;
    top: 0;
}

.article-banner {
    position: relative;
    width: 100%;
    height: 200px;
    transition: all 0.3s ease;
}

.article-banner.small {
    height: 100px;
}

.article-banner > img {
    z-index: -10;
}

.article-title {
    position: absolute;
    z-index: 10;
    width: 600px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease;
}

.article-title.small {
    font-size: 20px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.article-body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.article-paragraph {
    width: 600px;
    margin: 20px 0px;
    font-size: 20px;
    line-height: 1.5em;
}

.article-img {
    margin: 20px 0px;
    width: 600px;
    height: auto;
}